        boxOfficeRevenue: "12132",
        expenditure: "1321",
        grossProfit: "13124",
        netProfit: "132",
        audienceNumber: "12",
        averageFare: "121",
      },
    };
  },
  created() {
    var temp = new Date();
    this.todayDate += temp.getFullYear() + "-";
    this.todayDate += temp.getMonth() + 1;
    this.todayDate += "-" + temp.getDate();
  },
  methods: {},
};
</script>
